* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  background-color: var(--color-bg);
  font-family: "Inter", sans-serif;
  line-height: 1.5;
}

:root {
  --color-bg: #fff;
  --color-bg-card: #f0f2f9;
  --color-bg-card-hover: #e0e4f0;
  --facebook: #198ff5;
  --twitter: #1ca0f2;
  --ins-start: #fdc468;
  --ins-mid: #ee867c;
  --ins-end: #df4996;
  --youtube: #c4032a;
  --green: #1db489;
  --red: #dc414c;
  --dark-text: #1e202a;
  --light-text: #63677e;
  --toggle-light: #868cac;
  --toggle-btn-end: #3eda82;
  --toggle-btn-start: #378fe6;
}

.card {
  background-color: var(--color-bg-card);
  border-radius: 0.5rem;
  padding: 1.6rem;
  text-align: center;
  color: var(--light-text);
  overflow: hidden;
  cursor: pointer;
  transition: all 0.2s ease;
}
.card:hover {
  background-color: var(--color-bg-card-hover);
}
.card__username {
  font-size: 1.2rem;
  font-weight: 700;
}
.card__flatform {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0.5rem 0 2.8rem 0;
}
.card__followers {
  margin-bottom: 2.5rem;
}
.card__count {
  font-size: 5.6rem;
  color: var(--dark-text);
  font-weight: 700;
  letter-spacing: -2px;
  line-height: 1;
}
.card__label {
  font-size: 1.2rem;
  letter-spacing: 3px;
  font-weight: 400;
}
.card__icon {
  margin-right: 0.8rem;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  flex-wrap: nowrap;
  font-size: 1.2rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--green);
}
.card__change--down {
  color: var(--red);
}
.card__number {
  font-size: 1.2rem;
  margin-left: 0.4rem;
}
.card--facebook {
  border-top: 5px solid var(--facebook);
}
.card--twitter {
  border-top: 5px solid var(--twitter);
}
.card--youtube {
  border-top: 5px solid var(--youtube);
}
.card--instagram {
  position: relative;
}
.card--instagram::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  background: linear-gradient(to right, var(--ins-start), var(--ins-mid), var(--ins-end));
  width: 100%;
  height: 5px;
}

.card-grid__heading {
  margin-bottom: 2rem;
}
.card-grid__heading, .card-grid__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: nowrap;
}
.card-grid__title {
  font-size: 1.4rem;
  font-weight: 700;
  color: var(--light-text);
}
.card-grid__count {
  font-size: 3.2rem;
  font-weight: 700;
  color: var(--dark-text);
}
.card-grid__change {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  flex-wrap: nowrap;
}
.card-grid__change--up {
  color: var(--green);
}
.card-grid__change--down {
  color: var(--red);
}
.card-grid__number {
  font-size: 1.2rem;
  margin-left: 0.4rem;
  font-weight: 700;
}

.checkbox-toggle__input {
  display: none;
}
.checkbox-toggle__input:checked + .checkbox-toggle__label {
  background: linear-gradient(to right, var(--toggle-btn-start), var(--toggle-btn-end));
}
.checkbox-toggle__input:checked + .checkbox-toggle__label .checkbox-toggle__spin {
  transform: translateX(0);
  background-color: var(--dark-text);
}
.checkbox-toggle__label {
  width: 5rem;
  height: 2.5rem;
  border-radius: 3rem;
  display: inline-block;
  background-color: var(--toggle-light);
  padding: 0.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
}
.checkbox-toggle__spin {
  width: 1.5rem;
  height: 1.5rem;
  background-color: var(--color-bg-card);
  border-radius: 50%;
  transition: all 0.3s ease;
  transform: translateX(2.5rem);
}

.social-media {
  margin: 0 2rem;
}
.social-media__container {
  max-width: 102.4rem;
  margin: 0 auto;
}
.social-media__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 3.6rem 0 4.6rem 0;
}
@media screen and (max-width: 700px) {
  .social-media__header {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    flex-wrap: nowrap;
  }
}
.social-media .header__title h1 {
  font-size: 2.8rem;
  color: var(--dark-text);
}
.social-media .header__sub {
  font-size: 1.4rem;
  color: var(--light-text);
  font-weight: 700;
}
.social-media .header__toggle {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  flex-wrap: nowrap;
}
.social-media .header__toggle--title {
  font-size: 1.4rem;
  font-weight: 700;
  color: var(--toggle-light);
  margin-right: 1.6rem;
}
.social-media__overview h2 {
  margin-bottom: 2.4rem;
  font-size: 2.4rem;
  color: var(--light-text);
  line-height: 1.1;
}
.social-media__dashboard {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  margin-bottom: 4.6rem;
}
@media screen and (max-width: 1150px) {
  .social-media__dashboard {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}
.social-media .cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 2rem;
}
@media screen and (max-width: 1150px) {
  .social-media .cards {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 1fr);
  }
}
@media screen and (max-width: 700px) {
  .social-media__dashboard,
.social-media .cards {
    grid-template-columns: auto;
    grid-template-rows: auto;
  }
}

footer {
  font-size: 1.1rem;
  text-align: center;
  margin: 2rem 0;
}
footer a {
  font-size: inherit;
}